.figures {
    &__circle {
        width: 54px;
        height: 54px;
        border-radius: 50%;
        border: 8px solid $coral;
        position: absolute;
        top: 328px;
        left: -147px;
    }
    &__square {
        &-yellow {
            background: $yellow;
            width: 142px;
            height: 142px;
            border-radius: 12px;
            position: absolute;
            top: -55px;
            right: 75px;
            z-index: -1;
        }
        &-salmon {
            width: 216px;
            height: 635px;
            background: $salmon;
            filter: blur(150px);
            position: absolute;
            top: 32px;
            right: 112px;
            z-index: -1;
        }
        &-blue {
            width: 258px;
            height: 614px;
            background: $teal;
            filter: blur(100px);
            position: absolute;
            top: 43px;
            left: -43px;
            z-index: -1;
        }
    }
    &__ellipse {
        padding: 15px;
        &::after {
            content: '';
            width: 9px;
            height: 9px;
            background: $turquoise;
            border-radius: 50%;
            display: flex;
        }
        &-position {
            position: absolute;
            bottom: -93px;
            left: -97px;
            z-index: -1;
        }
    }
}