* {
  box-sizing: border-box;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.wrapper {
  max-width: 1680px;
  min-width: 375px;
  padding: 0 15px;
  margin: 0 auto;
}

.photo {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Noto Serif", "Bookman Old Style", serif;
  margin: 0;
  font-weight: 400;
  font-style: normal;
}

a {
  text-decoration: none;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-top: 39px;
  margin-bottom: 36px;
}

.header a {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 25px;
  color: #2c2c2c;
}

.header a:hover {
  color: #ff715b;
}

.header__account {
  display: flex;
  align-items: center;
}

.header__account svg {
  margin-right: 10px;
}

.header__search {
  background: url("../img/serch.svg");
  width: 21px;
  height: 21px;
  background-color: transparent;
  background-position: center center;
  border: none;
  margin-left: 35px;
  position: relative;
  cursor: pointer;
}

.header__search::before {
  content: "";
  border-left: 1px solid #f1e0dd;
  width: 0;
  height: 20px;
  position: absolute;
  top: 0;
  left: -15px;
  cursor: auto;
}

.main-menu {
  display: flex;
}

.main-menu__item {
  list-style: none;
}

.main-menu__item:not(:last-child) {
  margin-right: 40px;
}

.travel-now__list {
  position: relative;
  display: flex;
  align-items: center;
  background: url("../img/hero-banner.jpg");
  width: 100%;
  height: 1000px;
  margin-bottom: 20px;
}

.travel-now__name {
  padding-left: 8%;
}

.travel-now__name-header {
  font-weight: bold;
  font-size: 84px;
  line-height: 112px;
  max-width: 755px;
  margin: 0;
  align-items: center;
  color: #2c2c2c;
  padding-bottom: 50px;
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .travel-now__list {
    background-image: url("../img/hero-banner@2x.jpg");
  }
}

.button {
  cursor: pointer;
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  color: white;
  background: #ff715b;
  padding: 18px 36.5px;
}

.button:hover {
  background: #f3553d;
}

.button.button-form {
  padding: 20px 37px;
}

.button.button-destinations {
  padding: 20px 34px;
}

.travel-form {
  width: 70.5%;
  background: white;
  box-shadow: 0px 10px 30px 5px rgba(219, 200, 197, 0.2);
  display: flex;
  align-items: center;
  position: absolute;
  bottom: -77px;
  right: 0px;
  padding: 45px;
}

.travel-form__filter {
  border: none;
  width: 81.6%;
}

.explore {
  padding-top: 5%;
  display: flex;
  justify-content: center;
}

.explore__list {
  padding: 0;
  margin: 0;
  max-width: 1394px;
  display: flex;
  justify-content: center;
}

.explore__item {
  cursor: pointer;
  padding: 30px 40px;
  background: #f8f8f8;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.explore__item:not(:last-child) {
  margin-right: 30px;
}

.explore__item:hover {
  background: white;
  filter: drop-shadow(0px 12px 30px rgba(200, 180, 180, 0.2));
}

.explore__photo {
  height: 50%;
}

.explore__photo img {
  align-items: center;
  justify-content: center;
}

.explore__text {
  height: 50%;
}

.explore__text h3 {
  text-align: center;
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  color: #2c2c2c;
  margin: 0;
}

.explore__text p {
  text-align: center;
  margin: 20px 0 30px 0;
}

.arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 47px;
  height: 47px;
  border: 2px solid transparent;
  background-color: #ff715b;
}

.arrow:hover {
  background-color: #f3553d;
}

.arrow:not(:last-child) {
  margin-right: 20px;
}

.arrow-right {
  background: transparent;
  border-color: #f5ebe9;
}

.arrow-right:hover {
  background-color: #f5ebe9;
}

.slider {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 26%;
}

.slider__arrow {
  display: flex;
}

.slider span {
  width: 53%;
}

.slider__counter {
  font-weight: 500;
  font-size: 24px;
  line-height: 33px;
  color: #ff715b;
}

.top-destinations {
  position: relative;
  display: flex;
  margin-top: 16%;
}

.top-destinations::after {
  content: "";
  background: #f6f6f6;
  width: 789px;
  height: 580px;
  position: absolute;
  top: -25%;
  right: -5.57%;
  z-index: -1;
}

.top-destinations__content {
  width: 25%;
  margin-right: 5%;
}

.top-destinations__content h2 {
  padding-bottom: 20px;
}

.top-destinations__content span {
  color: #ff715b;
}

.top-destinations__content p {
  padding-top: 20px;
  padding-bottom: 60px;
}

.top-destinations__country {
  display: flex;
  width: 75%;
}

.top-destinations__img {
  position: relative;
  cursor: pointer;
}

.top-destinations__img:hover {
  transform: scale(1.05);
  transition-duration: 0.6s;
  transition-timing-function: linear;
}

.top-destinations__img:not(:last-child) {
  margin-right: 30px;
}

.top-destinations__img::after {
  content: "";
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 47.69%, rgba(0, 0, 0, 0.8) 100%);
  width: 100%;
  height: 99%;
  position: absolute;
  top: 0;
  left: 0;
}

.top-destinations__img span {
  position: absolute;
  bottom: 30px;
  left: 30px;
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  color: white;
  z-index: 1;
}

.heading {
  margin: 0;
  font-weight: 800;
  font-size: 48px;
  line-height: 65px;
  color: #2c2c2c;
}

.text-lexend {
  font-family: "Lexend Deca", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 26px;
  color: #a79997;
}

.line {
  height: 2px;
  background: #f5ebe9;
}

.out-world {
  display: flex;
  justify-content: center;
  margin-top: 325px;
}

.out-world__list {
  max-width: 1394px;
  display: flex;
  justify-content: center;
}

.out-world__img {
  position: relative;
  width: 50%;
}

.out-world__content {
  width: 50%;
}

.out-world__content h2 {
  margin-top: 43px;
}

.out-world__text {
  display: flex;
  flex-direction: column;
  margin-top: 43px;
}

.out-world__element {
  position: relative;
  margin-top: 45px;
}

.out-world__element p {
  display: none;
  max-width: 600px;
  padding-left: 12%;
}

.out-world__element:not(:last-child)::after {
  content: '';
  height: 2px;
  width: 600px;
  background: #f5ebe9;
  display: block;
}

.out-world__heading {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.out-world__heading h3 {
  font-family: "Noto Serif", "Bookman Old Style", serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 33px;
  color: black;
}

.out-world__heading:hover + .block {
  display: block;
  transition-duration: 1s;
}

.out-world__heading:hover .out-world__icon-orange {
  box-shadow: 0px 10px 15px rgba(255, 113, 91, 0.25);
  transition-duration: 0.4s;
}

.out-world__heading:hover .out-world__icon-green {
  box-shadow: 0px 10px 15px rgba(237, 250, 227, 0.25);
  transition-duration: 0.4s;
}

.out-world__heading:hover .out-world__icon-yellow {
  box-shadow: 0px 10px 15px rgba(255, 248, 227, 0.25);
  transition-duration: 0.4s;
}

.out-world__icon {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  position: relative;
  margin-right: 40px;
}

.out-world__icon-orange {
  background: #ff715b;
}

.out-world__icon-orange::after {
  content: '';
  background-image: url("../img/human_head.svg");
  width: 13px;
  height: 13px;
  position: absolute;
  top: 30%;
  left: 40%;
}

.out-world__icon-orange::before {
  content: '';
  background-image: url("../img/human_body.svg");
  width: 24px;
  height: 10px;
  position: absolute;
  top: 60%;
  left: 30%;
}

.out-world__icon-green {
  background: #edfae3;
}

.out-world__icon-green::before {
  content: '';
  background-image: url("../img/world.svg");
  width: 25px;
  height: 25px;
  position: absolute;
  top: 23%;
  left: 23%;
}

.out-world__icon-yellow {
  background: #fff8e3;
}

.out-world__icon-yellow::before {
  content: '';
  background: url("../img/headphones.svg");
  width: 25px;
  height: 25px;
  position: absolute;
  top: 23%;
  left: 23%;
}

.figures__circle {
  width: 54px;
  height: 54px;
  border-radius: 50%;
  border: 8px solid #ff5b65;
  position: absolute;
  top: 328px;
  left: -147px;
}

.figures__square-yellow {
  background: #ffca45;
  width: 142px;
  height: 142px;
  border-radius: 12px;
  position: absolute;
  top: -55px;
  right: 75px;
  z-index: -1;
}

.figures__square-salmon {
  width: 216px;
  height: 635px;
  background: #ffcbc3;
  filter: blur(150px);
  position: absolute;
  top: 32px;
  right: 112px;
  z-index: -1;
}

.figures__square-blue {
  width: 258px;
  height: 614px;
  background: #d3e2ff;
  filter: blur(100px);
  position: absolute;
  top: 43px;
  left: -43px;
  z-index: -1;
}

.figures__ellipse {
  padding: 15px;
}

.figures__ellipse::after {
  content: '';
  width: 9px;
  height: 9px;
  background: #a39ef0;
  border-radius: 50%;
  display: flex;
}

.figures__ellipse-position {
  position: absolute;
  bottom: -93px;
  left: -97px;
  z-index: -1;
}
