.out-world {
    display: flex;
    justify-content: center;
    margin-top: 325px;;
    &__list {
        max-width: 1394px;
        display: flex;
        justify-content: center;
    }
    &__img {
        position: relative;
        width: 50%;
    }
    &__content {
        width: 50%;
        & h2 {
            margin-top: 43px;
        }
    }
    &__text {
        display: flex;
        flex-direction: column;
        margin-top: 43px;
    }
    &__element{
        position: relative;
        margin-top: 45px;
        & p {
            display: none;
            max-width: 600px;
            padding-left: 12%;
        }
        &:not(:last-child) {
            &::after {
                content: '';
                height: 2px;
                width: 600px;
                background: $milk;
                display: block;
            }
        }
    }
    &__heading {
        cursor: pointer;
        display: flex;
        align-items: center;
        & h3 {
            font-family: "Noto Serif", "Bookman Old Style", serif;
            font-weight: 600;
            font-size: 24px;
            line-height: 33px;
            color: $black2;
        }
        &:hover {
            & + .block {
                display: block;
                transition-duration: 1s;
            }
            & .out-world__icon-orange {
                box-shadow: 0px 10px 15px rgba(255, 113, 91, 0.25);
                transition-duration: 0.4s;
            }
            & .out-world__icon-green {
                box-shadow: 0px 10px 15px rgba(237, 250, 227, 0.25);
                transition-duration: 0.4s;
            }
            & .out-world__icon-yellow {
                box-shadow: 0px 10px 15px rgba(255, 248, 227, 0.25);
                transition-duration: 0.4s;
            }
        }
    }
    &__icon {
        width: 50px;
        height: 50px;
        border-radius: 10px;
        position: relative;
        margin-right: 40px;
        &-orange {
            background: $orange;
            &::after {
                content: '';
                background-image: url('../img/human_head.svg');
                width: 13px;
                height: 13px;
                position: absolute;
                top: 30%;
                left: 40%;
            }
            &::before {
                content: '';
                background-image: url('../img/human_body.svg');
                width: 24px;
                height: 10px;
                position: absolute;
                top: 60%;
                left: 30%;
            }
        }
        &-green {
            background: $green;
            &::before {
                content: '';
                background-image: url('../img/world.svg');
                width: 25px;
                height: 25px;
                position: absolute;
                top: 23%;
                left: 23%;
            }
        }
        &-yellow {
            background: $yellow2;
            &::before {
                content: '';
                background: url('../img/headphones.svg');
                width: 25px;
                height: 25px;
                position: absolute;
                top: 23%;
                left: 23%;
            }
        }
    }
}